import React, { useState, useEffect } from 'react';
import { Nav, Navbar, NavDropdown } from 'react-bootstrap';
import {
    Link
} from "react-router-dom";
export default function MyNavbar(props) {
    
    const [auth, setAuth] = useState(false);
    const [me,setMe] = useState({});
    console.log(props)
   useEffect(() => {
       getme()
   }, [])

      function  getme(){
        const jwt = localStorage.getItem('jwt');
         fetch('/api/v1/me', {
                headers: { Authorization: `Bearer ${jwt}`, }
            })
            .then((user)=>{
                console.log("user")
                console.log(user)
                if(user.status ==200)
                    setAuth(true)
                return (user.json())
            })
            .then((user)=>{
                setMe(user)
            })
        }
            console.log("auth")
        console.log(auth)
        let profile
        let users
        console.log(me)
        if(me!={})
            profile=<Nav.Link><Link   class="no_link" to={"/users/"+me.id}>{me.login}</Link ></Nav.Link>
        let lastButtons
        if( me.role == 1)
            users=<Nav.Link><Link   class="no_link" to="/users">users</Link ></Nav.Link>
        if(!auth)
            lastButtons= <Nav key={auth}>
            <Nav.Link><Link  class="no_link"to="/login">login</Link ></Nav.Link>
            <Nav.Link><Link  class="no_link" to="/register">sign up</Link ></Nav.Link>
        </Nav>
        else 
            lastButtons=<Nav key={auth}><Nav.Link><Link   class="no_link" to="/logout">logout</Link ></Nav.Link></Nav>
         return (
        <Navbar collapseOnSelect expand="lg" bg="light" >
            <Navbar.Brand ><Link class="no_link" to="/"><img src="https://res.cloudinary.com/dksfym6ok/image/upload/v1576428172/Screenshot_from_2019-12-15_18-42-12_vbqdkh.png" weign="40" height="40"/></Link></Navbar.Brand>
            <Navbar.Toggle aria-controls="responsive-navbar-nav" />
            <Navbar.Collapse id="responsive-navbar-nav">
                <Nav className="mr-auto">
                    {profile}
                    {users}
                    <NavDropdown title="photos" id="collasible-nav-dropdown">
                    <NavDropdown.Item ><Link class="no_link" to="/photos">all</Link></NavDropdown.Item>
                    <NavDropdown.Item><Link class="no_link" to="/myphotos">my</Link></NavDropdown.Item>
                        <NavDropdown.Divider />
                        <NavDropdown.Item><Link class="no_link" to="/newphoto">new</Link></NavDropdown.Item>
                    </NavDropdown>
                    <NavDropdown title="albums" id="collasible-nav-dropdown">
                    <NavDropdown.Item ><Link class="no_link" to="/albums">all</Link></NavDropdown.Item>
                        <NavDropdown.Divider />
                    <NavDropdown.Item to="/newphoto"><Link class="no_link" to="/newalbum">new</Link></NavDropdown.Item>
                    </NavDropdown>
                    <Nav.Link ><Link class="no_link"  to="/about">about</Link ></Nav.Link>
                </Nav>
                {lastButtons}
            </Navbar.Collapse>
        </Navbar>
        
    )
}