
import React from 'react';
import './albums.css'
import NoAuth from '../errorPages/noAuth'
class NewAlbum extends React.Component {
    constructor() {
        super();
        this.state = {
            photoNames:[],
            button: false,
            serverError: false,
            auth:false,
            authSetted:false
        };
        this.checkAuth()

        this.handleSubmit = this.handleSubmit.bind(this);
        this.handlePhotoChange = this.handlePhotoChange.bind(this);
    }
    async checkAuth(){
        const jwt = localStorage.getItem('jwt');
        if (!jwt) {
            await this.setState({
                authSetted:true
            })
            return
        }

        const reqOptions = {
            headers: { Authorization: `Bearer ${jwt}`,'Content-Type': 'application/json',
            'Accept': 'application/json' },
        };
        let user=await fetch("/api/v1/photonames",reqOptions);
        console.log(user)
        console.log(user.body)
        console.log("its -")
        if (user.ok){
            console.log("its ok")
            let names= await user.json()
            console.log("its after json")
            console.log(names)
            await this.setState({
                authSetted:true,
                auth:true,
                photoNames:names
            })
            console.log(this.state.photos)
        }else{
            await this.setState({
                authSetted:true
            })
            console.log("wtf")
        }

    }


    handlePhotoChange(e) {
        console.log("changed")
        if (document.getElementById("inputGroupFile02").value!=''){
        let text=document.getElementById("inputGroupFile02").value.replace('C:\\fakepath\\', " ");
        document.getElementById("need").innerText=text
        }else{
            document.getElementById("need").innerText="Choose file*"
        }
    } 
    async handleSubmit(e) {
        e.preventDefault();
        const jwt = localStorage.getItem('jwt');
        if (!jwt) {
            await this.setState({
                authSetted:true,
                auth:false
            })
        }
        const reqOptions = {
            headers: { Authorization: `Bearer ${jwt}`, },
        };
        const formData = new FormData(e.target);
        try {
            let response = await fetch('/api/v1/albums/', {
                method: 'POST',
                body: formData,
                headers: { Authorization: `Bearer ${jwt}`, }
            });
            if(response.ok){
                this.props.history.push("/albums")
            }
            const result = await response.json();
            console.log(':', JSON.stringify(result));
            if (result.error)
                if(result.status=401)
                    await this.setState({
                        authSetted:true,
                        auth:false
                    })
                if (result.error == "there is already photo with such name")
                    await this.setState({
                        serverError: result.error
                    })
                else {
                    await this.setState({
                        serverError: "bad input"
                    })
                }
        } catch (error) {
            console.error(':', error);
        }
    }

    render() {
        if(!this.state.authSetted)
          return(<div></div>)
        if(!this.state.auth)
            return <NoAuth />
        let errorText
        if(this.state.serverError)
            errorText=<div class="errorBox">{this.state.serverError}</div>
        return (
            <div class="newPhotoStyle">
                <br />
                <br /><br />
                <p class="font-weight-bolder">Create new album</p>

                
                <form id="form" onSubmit={this.handleSubmit} enctype="multipart/form-data">
    <div class="input-group mb-3">
        <div class="input-group-prepend">
            <span class="input-group-text" id="inputGroup-sizing-default">name</span>
        </div>
        <input type="text"  name="name" placeholder="name" required class="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default"/>
    </div>
    
    <p class="font-weight-bolder">season:</p>
    
    <div class="form-check-inline">
      <label class="form-check-label" for="radio1">
        <input type="radio" class="form-check-input" id="radio1" name="season" value="winter"  />winter
      </label>
    </div>
    <div class="form-check-inline">
      <label class="form-check-label" for="radio2">
        <input type="radio" class="form-check-input" id="radio2" name="season" value="spring"/>spring
      </label>
    </div>
    <div class="form-check-inline">
      <label class="form-check-label" for="radio3">
        <input type="radio" class="form-check-input" id="radio3" name="season" value="summer"/>summer
      </label>
    </div>
    <div class="form-check-inline">
      <label class="form-check-label" for="radio4">
        <input type="radio" class="form-check-input" id="radio4" name="season" value="autumn"/>autumn
      </label>
    </div>
    <br/>
<br/>
    <p class="font-weight-bolder">choose photos:</p>

    {this.state.photoNames.map(photo=>{
        console.log("yaya")
        return(
            <div class="form-check-inline">
                <label class="form-check-label">
                    <input type="checkbox" class="form-check-input" name={photo.id}/>{photo.name}
                </label>
            </div>
    )})}
    <br/>
    <br/>
        <div class="input-group mb-3">
            <div class="custom-file">
                <input type="file" class="custom-file-input" name ="previewUrl" accept=".jpg,.jpeg,.png" id="inputGroupFile02" />
                <label class="custom-file-label" for="inputGroupFile02">Choose file</label>
            </div>
        </div>
        
    
    <div class="input-group mb-3">
        <div class="input-group-prepend">
            <span class="input-group-text" id="inputGroup-sizing-default">comment</span>
        </div>
        <textarea type="text"  name="description" placeholder="/description" required class="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default"></textarea>
    </div>
    <button class="btn btn btn-primary btn-block" type="submit">post</button>
  </form>
                    </div>


                    );
                  }
              }
            
  export default NewAlbum;