
import React  from 'react';
import   './photos.css'
import {
    Link
  } from "react-router-dom";
  import Modal from 'react-bootstrap/Modal'
  import Button from 'react-bootstrap/Button'
  import NoAuthErr from '../errorPages/noAuth';
  import NotFound from '../errorPages/notFound';
class Photo extends React.Component {

        constructor(props) {
        super(props);
        this.state = {
            photo:{},
            user:{},
            editButton:false,
            deleteButton:false,
            buyButton:false,
            errorStatus:200,
            modalShow:false,
            authSetted:false
        };
        this.handleBuy = this.handleBuy.bind(this);
        this.handleDelete = this.handleDelete.bind(this);
        this.getPhoto(props.match.params.id)
      }
      async handleBuy(){
        console.log("bought")
        const jwt = localStorage.getItem('jwt');
        if (!jwt) {
            await this.setState({
                errorStatus:"401"
            })
        }
        const reqOptions = {
            method:"PUT",
            headers: { Authorization: `Bearer ${jwt}`, },
        };
        
        let user=await fetch("/api/v1/photos/"+this.props.match.params.id+"?owner="+this.state.user.id,reqOptions);
        await this.setState({
          buyButton:false,
          editButton:true,
          deleteButton:true
        })
      }
      async getPhoto(id){
        const jwt = localStorage.getItem('jwt');
        if (!jwt) {
            await this.setState({
              authSetted:true,
                errorStatus:"401"
            })
        }
        const reqOptions = {
            headers: { Authorization: `Bearer ${jwt}`, },
        };
        
        let user=await fetch("/api/v1/me/",reqOptions);
        if (user.ok){
            user=await user.json();
            console.log("user")
            console.log(user)
            await this.setState({authSetted:true,user:user})
        }else{
            this.setState({
              authSetted:true,
                errorStatus:"401"
            })
        }
        let response= await fetch("/api/v1/photos/"+id,reqOptions);
        if (response.ok){
            response=await response.json();
            console.log(response)
            let options = {
              era: 'short',
              year: 'numeric',
              month: 'long',
              day: 'numeric',
              weekday: 'long',
              timezone: 'UTC',
              hour: 'numeric',
              minute: 'numeric',
              second: 'numeric'
            };
            let date= Date.parse(response.date)
            date=new Date(date)
            response.date=date.toLocaleString("en-US",options)
            await this.setState({photo:response})
        }
        else 
            if(response.status==404){
                console.log("404")
                await this.setState({
                    errorStatus:404
                })
                return
            }else{
                if(response.status==401){
                    console.log("401")
                    await this.setState({
                      authSetted:true,
                        errorStatus:401
                    })
                    return
                }
            }


        
        let condition=false
        if(this.state.photo)
            if(this.state.photo.owner)
                condition=(this.state.photo.owner.toString()==this.state.user.id.toString() )||this.state.user.role
        if(this.state.photo.owner.toString()!=this.state.user.id.toString())
            await this.setState({buyButton:true})
        if(this.state.user.role)
            condition=true
        await this.setState({
            editButton:condition,
            deleteButton:condition
        })
      }

      async handleDelete(event){
        console.log("event")
    }
    render() 
      {
        if(!this.state.authSetted)
            return(<div></div>)
        console.log(this.state.errorStatus)
          if(this.state.errorStatus==401)
            return(<NoAuthErr/>)
            if(this.state.errorStatus==404)
            return(<NotFound/>)
          let buttons;
        if (this.state.editButton){
            buttons=<div class="padik"><Link to={'/editphoto/'+this.state.photo._id}> <button class="btn btn-outline-dark inlineBlock"  type="submit" value="Delete">Edit</button></Link>
            <button style={{marginLeft:".4em"}} class="btn btn-outline-dark inlineBlock" onClick={() => this.setState({modalShow:true})}  value="Delete">Delete</button></div>
        }
        let buyButton
          if(this.state.buyButton)
        buyButton=<button class="btn btn-outline-dark inlineBlock" onClick={this.handleBuy}  value="Buy">Buy</button>
        return (
            
            <div class="photoContainer">
        <div class="photo"><img src={this.state.photo.url} class="img-thumbnail photo1 "/></div>
        <div class="photoInfo">
            Name: {this.state.photo.name}
            <p>Author: {this.state.photo.author}</p>
            <p>price :{this.state.photo.price} $</p>
            {/* <p>Owner: {this.state.photo.owner}</p> */}
            <p>Date: {this.state.photo.date}</p>
             {buttons} {buyButton}
         </div>
         <MyVerticallyCenteredModal
        show={this.state.modalShow}
        onHide={() => this.setState({modalShow:false})}
        id={this.props.match.params.id}
        history={this.props.history}
      />

     </div>
        );
      }}
  
  export default Photo;

  function MyVerticallyCenteredModal(props) {
      console.log(props)
      let myHandler=(e)=> {
          console.log("delete");
          const jwt = localStorage.getItem('jwt');
        const reqOptions = {
            headers: { Authorization: `Bearer ${jwt}`, },
            method:'DELETE'
        };
        
        fetch("/api/v1/photos/"+props.id,reqOptions)
            .then(response=>{
                props.history.push("/photos")
                console.log(response.json()) 
            })

        
      }
    return (
      <Modal
        {...props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header closeButton>
          <Modal.Title id="contained-modal-title-vcenter">
          Confirm deletion
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          
          <p>
            you won't have access to this photo any more
          </p>
        </Modal.Body>
        <Modal.Footer>
            <Button onClick={myHandler}>Delete</Button>
          <Button onClick={props.onHide}>Close</Button>
        </Modal.Footer>
      </Modal>
    );
  }