
import React, { useState, useEffect } from 'react';
import   './users.css'
import NoAuth from '../errorPages/noAuth'
import NoAdmin from '../errorPages/noAdmin'
import {
    Link
  } from "react-router-dom";
  let options = {
    era: 'short',
    year: 'numeric',
    month: 'long',
    day: 'numeric',
    weekday: 'long',
    timezone: 'UTC',
    hour: 'numeric',
    minute: 'numeric',
    second: 'numeric'
  };
class Users extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            page:1,
            tag:'',
            pageNum:1,
            nextPage:false,
            prevPage:true,
            listUsers:[],
            authSetted:false,
            auth:false,
            adm:false
        };
        this.proceedState();
        this.handleTagChange = this.handleTagChange.bind(this);
        this.handleNext = this.handleNext.bind(this);
        this.handlePrev = this.handlePrev.bind(this);
      }
      async proceedState(){
        console.log("ok boomer")
        const jwt = localStorage.getItem('jwt');
        if (!jwt) {
            await this.setState({
                authSetted:true,
                auth:false
            })
            return
        }

        const reqOptions = {
            headers: { Authorization: `Bearer ${jwt}`, },
        };
        Promise.all([
            fetch("/api/v1/users?searching="+this.state.tag+"&page="+this.state.page,reqOptions),
            fetch("/api/v1/me/",reqOptions)
        ])
            .then(async ([itemsData,me]) => {
                itemsData=await itemsData.json();
                if (me.ok){
                    await this.setState({authSetted:true,auth:true})
                }else{
                    await this.setState({authSetted:true,auth:false})
                    return
                }
                let tmpUser=await me.json()
                console.log("tmpUser")
                console.log(tmpUser)
                if(tmpUser.role==1)
                    await this.setState({adm:true})
                console.log("num ofpages")
                console.log(itemsData.pageNumber)
                await this.setState({pageNum:itemsData.pageNumber})
                if (parseInt(this.state.page)>1)
                    await this.setState({prevPage:true});
                else    
                    await this.setState({prevPage:false});
                if(this.state.page<this.state.pageNum)
                    await this.setState({nextPage:true});
                else
                    await this.setState({nextPage:false});
                console.log(itemsData.displayData)
                let options = {
                    era: 'short',
                    year: 'numeric',
                    month: 'long',
                    day: 'numeric',
                    weekday: 'long',
                    timezone: 'UTC',
                    hour: 'numeric',
                    minute: 'numeric',
                    second: 'numeric'
                  };
                if(itemsData.displayData!=undefined){
                    for(let i=0;i<itemsData.displayData.length;i++)
                        if(itemsData.displayData[i]){
                            itemsData.displayData[i].num=3*this.state.page-2+i
                            let date= Date.parse(itemsData.displayData[i].registeredAt)
                            date=new Date(date)
                            itemsData.displayData[i].registeredAt=date.toLocaleString("en-US",options)
                        }
                    this.setState({listUsers: itemsData.displayData})
                }
                else
                this.setState({listUsers:[],pageNum:1})

                   console.log( this.state.listUsers)
            })
        .catch(async err=>{
            await this.setState({authSetted:true,auth:false})
        })      
      }
      async handleTagChange(event){
          console.log("event")
          await this.setState({
            tag:event.target.value,
            page:1
          })
          console.log(this.state.tag)
          await this.proceedState();
      }
      async handleNext(event){
        console.log("event")
        let page=this.state.page+1
        await this.setState({
          page:page
        })
        await this.proceedState();
    }
    async handlePrev(event){
        console.log("event")
        let page=this.state.page-1
        await this.setState({
          page:page
        })
        await this.proceedState();
    }
      render(
          
      ) {
        if(!this.state.authSetted)
        return(<div></div>)
        console.log("this.state.auth")
        console.log(this.state.auth)
        if(!this.state.auth)
            return <NoAuth />
        if(!this.state.adm)
            return <NoAdmin />
        let button1;
        if (this.state.prevPage)
            button1=<button onClick={this.handlePrev} id="prevButton"class="btn btn-outline-dark" >prev</button>
        else    
            button1=<button onclick="prev()" id="prevButton"class="btn btn-outline-dark" disabled>prev</button>
            let button2;
        if (this.state.nextPage)
            button2=<button onClick={this.handleNext} id="prevButton"class="btn btn-outline-dark" >next</button>
        else    
            button2=<button id="prevButton"class="btn btn-outline-dark" disabled>next</button>
        return (
            <div>
                <br/>
  <div class="form-inline mt-2 mt-md-0 marginlelt">
    <label for="searh" id="heading" >current user list:
    </label>
      <input onChange={this.handleTagChange}  class="form-control mr-sm-2 marginleltmore" id="search"type="text" placeholder="Search"  name="searching"/>
    </div>
    <br/>   
    <div class="tableUsers">
                <table class="table">
                <thead>
                    <tr>
                    <th scope="col">#</th>
                    <th scope="col">login</th>
                    <th scope="col">fullname</th>
                    <th scope="col">registretedAt</th>
                    </tr>
                </thead>
                <tbody>
                {this.state.listUsers.map(user=>(
                    <tr>
                        <th scope="row">{user.num}</th>
                        <td><Link to={'/users/'+user.id}>{user.login}</Link></td>
                        <td>{user.fullname}</td>
                        <td>{user.registeredAt}</td>
                    </tr>

                ))}
                </tbody>
                </table></div>
                <div style={{"text-align":"center"}}>
    {button1}
      <span id="page">{this.state.page}</span>/<span id="pagenum">{this.state.pageNum}</span>
    {button2}
  </div> 
            </div>
        )
      }
  }
  export default Users;