
import React from 'react';
import './users.css'
class EditUser extends React.Component {

    constructor(props) {
        super(props);
        console.log(props.match.params.id);
        this.getUser(props.match.params.id)
        this.state = {
            previewUser: {},
            user: {},
            error: false,
            can: false,
            serverError:false,
            authSetted:false
        };
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handlePriceInput=this.handlePriceInput.bind(this)
        this.handleLoginInput=this.handleLoginInput.bind(this)
        this.handleFullnameInput=this.handleFullnameInput.bind(this)
        this.handleAvaChange = this.handleAvaChange.bind(this);
    }
    async handlePriceInput(e){
        let previewUser=this.state.previewUser
        previewUser.price=e.target.value
        await this.setState({ previewUser:previewUser  })
    }
    handleAvaChange(e) {
        console.log("changed")
        if (document.getElementById("inputGroupFile02").value!=''){
        let text=document.getElementById("inputGroupFile02").value.replace('C:\\fakepath\\', " ");
        document.getElementById("need").innerText=text
  
        }else{
            document.getElementById("need").innerText="Choose file"
        }
      }
    async handleSubmit(e){
        e.preventDefault(); 
        const formData = new FormData(e.target);
        const jwt = localStorage.getItem('jwt');
        if (!jwt) {
            alert("JWT is missing. Log in first");
            return;
        }
        try {
            const response = await fetch('/api/v1/users/'+this.props.match.params.id, {
              method: 'PUT',
              body: formData,
              headers: { Authorization: `Bearer ${jwt}`, },
            });
            const result = await response.json();
            console.log('response:', JSON.stringify(result));
            if (result.error)
                if(result.error=="there is already previewUser with such login")
                    this.setState({serverError:result.error})
                else {
                    this.setState({serverError:"bad input"})
                }
            else(this.props.history.push("/users/"+this.props.match.params.id))
          } catch (error) {

          }
    }
    async handleLoginInput(e){
        let previewUser=this.state.previewUser
        previewUser.login=e.target.value
        await this.setState({ previewUser:previewUser  })
    }
    async handleFullnameInput(e){
        let previewUser=this.state.previewUser
        previewUser.fullname=e.target.value
        await this.setState({ previewUser:previewUser  })
    }
    async getUser(id) {
        const jwt = localStorage.getItem('jwt');
        if (!jwt) {
            await this.setState({authSetted:true})
            return;
        }
        const reqOptions = {
            headers: { Authorization: `Bearer ${jwt}`, },
        };

        let user = await fetch("/api/v1/me/", reqOptions);
        if (user.ok) {
            user = await user.json();
            console.log("user")
            console.log(user)
            await this.setState({authSetted:true})
            await this.setState({ user: user })
        } else {
            await this.setState({authSetted:true, error: "not authorized" })
            return
        }
        let response = await fetch("/api/v1/users/" + id, reqOptions);
        if (response.ok) {
            response = await response.json();
            console.log(response)
            if(!response.avaUrl)
                response.avaUrl="http://res.cloudinary.com/dksfym6ok/image/upload/v1575476141/asd.png"
            await this.setState({ previewUser: response })
        } else {
            console.log("hmm")
            await this.setState({ error:"no such previewUser" })
            return;
        }

        let condition = false
        if (this.state.previewUser)
            if (this.state.previewUser.owner)
                condition = (this.state.previewUser.owner.toString() == this.state.user.id.toString()) || this.state.user.role
        await this.setState({
            can: condition
        })
    }

    // async handleDelete(event) {
    //     console.log("event")

    // }

render()
{   
    if(!this.state.authSetted)
    return(<div></div>)
    return(
        <div class="container magrinTop30" >
                <div class="marginLeft20">
                <div class="row">
                    <div class="col-sm-4">

                    <div class="img"><img src={this.state.previewUser.avaUrl} class="img-thumbnail userImage" /></div>
                    </div>
                    <div class="col-sm-4" class="padiingLeft">
                        <br/>
                    <h3>Edit profile</h3>
                    
                    {this.state.serverError}
                    <form onSubmit={this.handleSubmit}>
                    <div class="input-group mb-3">
                        <div class="input-group-prepend">
                            <span class="input-group-text" id="inputGroup-sizing-default">login</span>
                        </div>
                        <input type="text" onChange={this.handleLoginInput} value={this.state.previewUser.login} login="login" placeholder="login" required class="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default"/>
                    </div>
                    <div class="input-group mb-3">
                        <div class="input-group-prepend">
                            <span class="input-group-text" id="inputGroup-sizing-default">fullname</span>
                        </div>
                        <input type="text" onChange={this.handleFullnameInput} value={this.state.previewUser.fullname} login="login" placeholder="login" required class="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default"/>
                    </div>
                    <div class="input-group mb-3">
                        <div class="custom-file">
                            <input type="file" class="custom-file-input" name="avaUrl" onChange={this.handleAvaChange} accept=".jpg,.jpeg,.png" id="inputGroupFile02" />
                            <label class="custom-file-label butt" id="need" for="inputGroupFile02">Choose file</label>
                        </div>
                    </div>
                    <button class="btn btn btn-primary btn-block" type="submit">save changes</button>
                    </form>
                    </div>
                </div>
                </div>
                </div>
    

    )
}}
                  
  export default EditUser;