
import React  from 'react';
import   './albums.css'
import {
    Link
  } from "react-router-dom";
  import Modal from 'react-bootstrap/Modal'
  import Button from 'react-bootstrap/Button'
  import NoAuthErr from '../errorPages/noAuth';
  import NotFound from '../errorPages/notFound';
class Album extends React.Component {

        constructor(props) {
        super(props);
        this.state = {
            album:{},
            displayPhotos:[],
            page:1,
            user:{},
            editButton:false,
            deleteButton:false,
            next:false,
            prev:false,
            errorStatus:200,
            modalShow:false,
            authSetted:false
        };
        this.prevPage = this.prevPage.bind(this);
        this.nextPage = this.nextPage.bind(this);
        this.handleDelete = this.handleDelete.bind(this);
        this.getAlbum(props.match.params.id)
      }
            
      async changePhotos(){
        const jwt = localStorage.getItem('jwt');
        if (!jwt) {
            await this.setState({
                errorStatus:"401",
                authSetted:true
            })
        }
        const reqOptions = {
            headers: { Authorization: `Bearer ${jwt}`, },
        };
        let photos=[]
        for(let i=this.state.page-1;i<this.state.page+3 && i<this.state.album.photos.length;i++){
            let photo=await fetch("/api/v1/photos/"+this.state.album.photos[i],reqOptions);
            photo=await photo.json();
            console.log(photo)
            photos.push(photo)
        }
        if (this.state.page>1)
            await this.setState({prev:true})
        else
            await this.setState({prev:false})
        if(this.state.page<this.state.album.photos.length-3)
            await this.setState({next:true})
        else
            await this.setState({next:false})
        await this.setState({displayPhotos:photos})

      }
      async prevPage(){
          await this.setState({page:this.state.page-1})
          await this.changePhotos()
      }
      async nextPage(){
        await this.setState({page:this.state.page+1})
        await this.changePhotos()
    }

      async getAlbum(id){
        const jwt = localStorage.getItem('jwt');
        if (!jwt) {
            await this.setState({
                errorStatus:"401"
            })
        }
        const reqOptions = {
            headers: { Authorization: `Bearer ${jwt}`, },
        };
        
        let user=await fetch("/api/v1/me/",reqOptions);
        if (user.ok){
            user=await user.json();
            console.log("user")
            console.log(user)
            await this.setState({user:user})
        }else{
            this.setState({
                errorStatus:"401"
            })
        }
        let response= await fetch("/api/v1/albums/"+id,reqOptions);
        if (response.ok){
            response=await response.json();
            console.log(response)
            await this.setState({album:response,authSetted:true})
        }
        else 
            if(response.status==404){
                console.log("404")
                await this.setState({
                    errorStatus:404
                })
                return
            }else{
                if(response.status==401){
                    console.log("401")
                    await this.setState({
                        errorStatus:401,
                        authSetted:true
                    })
                    return
                }
                else{
                    await this.setState({
                        authSetted:true
                    })
                }
            }
        await this.changePhotos();
        let condition=false
        if(this.state.album)
            if(this.state.album.owner)
                condition=(this.state.album.owner.toString()==this.state.user.id.toString() )||this.state.user.role
        if(this.state.user.role)
            condition=true
        await this.setState({
            editButton:condition,
            deleteButton:condition
        })
      }

      async handleDelete(event){
        console.log("event")
    }
    render() 
      {
          if(!this.state.authSetted)
          return(<div></div>)
        console.log(this.state.errorStatus)
          if(this.state.errorStatus==401)
            return(<NoAuthErr/>)
            if(this.state.errorStatus==404)
            return(<NotFound/>)
          let buttons;
        if (this.state.editButton){
            buttons=<button class="btn btn-outline-dark inlineBlock" onClick={() => this.setState({modalShow:true})}  value="Delete">Delete</button>
        }
        let prevButton
        if (this.state.prev){
            prevButton=<button class="btn btn-outline-dark inlineBlock vC" onClick={this.prevPage} value="prev">  </button>
        }
        let nextButton
        if (this.state.next){
            nextButton=<button class="btn btn-outline-dark inlineBlock vC" onClick={this.nextPage} value="next"> </button>
        }

        return (
            <div >
                <div class="jumbotron" style={{minHeight:"300px"}}>

            <div class="albumContainer">
        <div class="photo"><img src={this.state.album.previewUrl} class="img-thumbnail album1 "/></div>
        <div class="photoInfo">
            Name: {this.state.album.name}
            <p>Author: {this.state.album.author}</p>
             {buttons}
         </div>
         <MyVerticallyCenteredModal
        show={this.state.modalShow}
        onHide={() => this.setState({modalShow:false})}
        id={this.props.match.params.id}
        history={this.props.history}
      />
            </div>
            </div>
            <div class="row" style={{margin:"0"}}>
        <div class="col-md-2">{prevButton}</div>

    {this.state.displayPhotos.map(album => {
if (album != null) {
    console.log("album")
    console.log(album)
    return (

        <div class="col-md-2" style={{ display: "inline-block" }}>
            <div class="card mb-3 shadow-sm">
                <svg class="bd-placeholder-img card-img-top" width="100%" height="100%" xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="xMidYMid slice" focusable="false" role="img" aria-label="Placeholder: Thumbnail">
                    <image href={album.url} alt={album.name} width="100%" height="100%" x="0" y="0" />
                </svg>
                <div class="card-body">
                    <div class="d-flex justify-content-between align-items-center">
                        <div class="btn-group">
                            <Link to={'/photos/' + album._id} class="btn btn-sm btn-outline-secondary">View</Link>
                        </div>
                        <small class="text-muted">{album.name}</small>
                    </div>
                </div>
            </div>
        </div>

    )
} else
    return (<div class="chetv"></div>)
})}
<div class="col-md-2">{nextButton}</div>
            </div>
     </div>
        );
      }}
  
  export default Album;

  function MyVerticallyCenteredModal(props) {
      console.log(props)
      let myHandler=(e)=> {
          console.log("delete");
          const jwt = localStorage.getItem('jwt');
        const reqOptions = {
            headers: { Authorization: `Bearer ${jwt}`, },
            method:'DELETE'
        };
        
        fetch("/api/v1/albums/"+props.id,reqOptions)
            .then(response=>{
                props.history.push("/albums")
                console.log(response.json()) 
            })

        
      }
    return (
      <Modal
        {...props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header closeButton>
          <Modal.Title id="contained-modal-title-vcenter">
          Confirm deletion
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          
          <p>
            you won't have access to this album any more
          </p>
        </Modal.Body>
        <Modal.Footer>
            <Button onClick={myHandler}>Delete</Button>
          <Button onClick={props.onHide}>Close</Button>
        </Modal.Footer>
      </Modal>
    );
  }