import React from 'react';
import logo from './logo.svg';
import './App.css';
import {
  BrowserRouter ,
  Route,
  Switch,
  Link
} from "react-router-dom";
import MyFooter from './partials/footer'
import MyNavbar from './partials/navbar';
import About from './about/about';
import Home from './home/home';
import Login from './auth/login';
import Logout from './auth/logout';
import Register from './auth/register';
import Photos from './photos/photos';
import NewPhoto from './photos/newphoto';
import NewAlbum from './albums/newalbum';
import MyPhotos from './photos/myphotos';
import Photo from './photos/photo';
import Album from './albums/album';
import EditPhoto from './photos/editphoto';
import Albums from './albums/albums';
import Users from './users/users';
import User from './users/user';
import EditUser from './users/edituser';
import history from './hystory'
import Telega from './auth/telega'
import NotFound from './errorPages/notFound'
import dev from './dev'
function App() {
  return (
    <div id="App">
      <BrowserRouter>
      <MyNavbar/>
      
      <Switch>
          <Route path="/about" exact component={About} />
          <Route path="/" exact component={Home} />
          <Route path="/login" exact component={Login} />
          <Route path="/logout" exact component={Logout} />
          <Route path="/register" exact component={Register} />
          <Route path="/photos/:id" history={history} exact component={Photo} />
          <Route path="/albums/:id" history={history} exact component={Album} />
          <Route path="/photos" exact component={Photos} />
          <Route path="/newphoto" exact component={NewPhoto} />
          <Route path="/newalbum" exact component={NewAlbum} />
          <Route path="/editphoto/:id" exact component={EditPhoto} />
          <Route path="/myphotos" exact component={MyPhotos} />
          <Route path="/albums" exact component={Albums} />
          <Route path="/users" exact component={Users} />
          <Route path="/users/:id" exact component={User} />
          <Route path="/bot/auth" exact component={Telega} />
          <Route path="/editusers/:id" exact component={EditUser} />
          <Route path="/dev/v1" exact component={dev} />
          <Route path="*" component={NotFound} />
      </Switch>
      </BrowserRouter>
      <MyFooter/>
    </div>
  );
}

export default App;
