
import React from 'react';
import './login.css'

class RegisterForm extends React.Component {
    constructor() {
        super();
        this.state = {
            login:'',
            pass:'',
            pass1:'',
            fullname:'',
            file:'',
            button:false,
            serverError:false
            
        };
    
        this.handleLoginChange = this.handleLoginChange.bind(this);
        this.handlePassChange = this.handlePassChange.bind(this);
        this.handleFullnameChange = this.handleFullnameChange.bind(this);
        this.handlePass1Change = this.handlePass1Change.bind(this);
        this.handleAvaChange = this.handleAvaChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
      }
      
      async checkButton(){
        console.log("checking")
          if(this.state.login.length>2 && this.state.fullname.length>0&& this.state.pass.length>2 && this.state.pass==this.state.pass1){
                console.log("true")
               await this.setState({
                button: true
            });
          }
            else await this.setState({
              button: false
          });
      }

      async handleLoginChange(event) {
          console.log(event.target.value)
        await this.setState({
            login: event.target.value
        });
        this.checkButton()
      }
      async handleFullnameChange(event) {
        console.log(event.target.value)
      await this.setState({
          fullname: event.target.value
      });
      await this.checkButton()
    }
     async handlePassChange(event) {
        console.log(event.target.value)
        await this.setState({
            pass:event.target.value
        });
        await this.checkButton()
    }
     async handlePass1Change(event) {
      console.log(event.target.value)
      await this.setState({
          pass1:event.target.value
      });
      await this.checkButton()
      
  }
    handleAvaChange(e) {
      console.log("changed")
      if (document.getElementById("inputGroupFile02").value!=''){
      let text=document.getElementById("inputGroupFile02").value.replace('C:\\fakepath\\', " ");
      document.getElementById("need").innerText=text

      }else{
          document.getElementById("need").innerText="Choose file"
      }
    }
      async handleSubmit(e) {
        e.preventDefault(); 
        const formData = new FormData(e.target);
        try {
            const response = await fetch('/api/v1/users/', {
              method: 'POST',
              body: formData
            });
            if (response.ok)
              window.location.href = "/";
            const result = await response.json();
            
            console.log(':', JSON.stringify(result));
            if(result.error)
              if (result.error=="there is already photo with such name")
                await this.setState({
                  serverError:result.error
            })
            else{
              await this.setState({
                serverError:"bad input"
          })
            }
          } catch (error) {
            console.error(':', error);
          }
      }
    
      render() {
        let highText
        if(this.state.serverError)
          highText=<p class="font-weight-bolder">{this.state.serverError}</p>
        else
          highText=<p class="font-weight-bolder">sign up</p>
        
        let button
        if(this.state.button)
            button=<button class="btn btn-lg btn-primary btn-block" type="submit">sign up</button>
        else  
            button=<button class="btn btn-lg btn-primary btn-block" type="submit" disabled>sign up</button>
        return (
            <div id="registerForm">
            <form class="form-signin" onSubmit={this.handleSubmit} enctype="multipart/form-data">
                    {highText}
              <div class="input-group mb-3">
              <div class="input-group-prepend">
                <span class="input-group-text" id="inputGroup-sizing-default">login</span>
              </div>
              <input type="text" name="login" onChange={this.handleLoginChange}placeholder="Login" required class="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default"/>
            </div>
            
              <div class="input-group mb-3">
                    <div class="input-group-prepend">
                        <span class="input-group-text" id="inputGroup-sizing-default">fullname</span>
                    </div>
                    <input type="text" name="fullname" onChange={this.handleFullnameChange} placeholder="fullname" required class="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default"/>
                </div>
            
            <div class="input-group mb-3">
                        <div class="custom-file">
                            <input type="file" id="inputGroupFile02" onChange={this.handleAvaChange} class="custom-file-input" name ="avaUrl"accept=".jpg,.jpeg,.png" />
                            <label class="custom-file-label butt" id="need" for="inputGroupFile02">choose ava</label>
                        </div>
                    </div>
            <script src="./file.js"></script>
                
              <div class="form-label-group"><input type="password" onChange={this.handlePassChange}id="password" placeholder="password" name="password" required class="form-control"/>
                <label for="password"><span class="font-weight-lighter"> password</span>
                </label>
              </div>
              <div class="form-label-group"><input type="password" onChange={this.handlePass1Change} class="form-control" id="password1" placeholder="password" name="password1" required />
                <label for="password1"><span class="font-weight-lighter">confirm password</span>
                </label>
              </div>
              {button}
              <br/>
              <h6 class="h6 mb-3 font-weight-normal"><a href="/auth/login"> i already have the account</a> </h6>
            </form>
            </div>
        );
      }
  }
  export default RegisterForm;