
import React from 'react';

import './login.css'
class LoginForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            login:'',
            pass:''
        };
    
        this.handleLoginChange = this.handleLoginChange.bind(this);
        this.handlePassChange = this.handlePassChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
      }
    
      handleLoginChange(event) {
          console.log(event.target.value)
        this.setState({
            login: event.target.value
        });
      }
      handlePassChange(event) {
        console.log(event.target.value)
        this.setState({
            pass:event.target.value
        });
    }
      async handleSubmit(e) {
        e.preventDefault();  // cancel page reload
         fetch("/api/v1/auth/login?username="+this.state.login+"&password="+this.state.pass, { method: 'POST'})
            .then(x => x.json())
            .then(authResult => {
                console.log(authResult);
                const jwt = authResult.token;
                console.log(jwt)
                if (jwt==undefined){
                  document.getElementById("nadpis").innerText="No such user";
                }else{
                  localStorage.setItem("jwt", jwt); 
                  window.location.href = "/";
                }
                 
            })
            .catch((error)=>console.log(error));

        
        
      }
    
      render() {
        return (
            <div id="loginForm">
            <form class="form-signin" onSubmit={this.handleSubmit} id="login-form">
                <h1 class="h3 mb-3 font-weight-normal" id="nadpis">Please sign in</h1>
            
              <div class="input-group mb-3">
              <div class="input-group-prepend">
                <span class="input-group-text" id="inputGroup-sizing-default">login</span>
              </div>
              <input type="text" name="username" value={this.state.login} onChange={this.handleLoginChange} placeholder="Login" required class="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default"/>
            </div>
            
            
            
              <div class="input-group mb-3">
              <div class="input-group-prepend">
                <span class="input-group-text" id="inputGroup-sizing-default">pass</span>
              </div>
              <input type="password" name="password"value={this.state.pass} onChange={this.handlePassChange} class="form-control" placeholder="Password" required aria-label="Default" aria-describedby="inputGroup-sizing-default"/>
            </div>
            
              <button class="btn btn btn-primary btn-block" type="submit">Sign in</button>
              <br/>
              <h6 class="h6 mb-3 font-weight-normal">don't have account yet?<a href="/auth/register"> sing up</a> </h6>
            </form>
            </div>
        );
      }
  }
  export default LoginForm;