
import React from 'react';
import './photos.css'
import NoAuth from '../errorPages/noAuth'
class NewPhoto extends React.Component {
    constructor() {
        super();
        this.state = {
            name: '',
            pass: '',
            pass1: '',
            author: '',
            file: '',
            button: false,
            serverError: false,
            auth:true,
            authSetted:false
        };
        this.checkAuth()
        this.handleNameChange = this.handleNameChange.bind(this);
        this.handlePriceChange = this.handlePriceChange.bind(this);
        this.handleAuthorChange = this.handleAuthorChange.bind(this);
        this.handlePhotoChange = this.handlePhotoChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    async checkAuth(){
        const jwt = localStorage.getItem('jwt');
        if (!jwt) {
            await this.setState({
                authSetted:true,
                auth:false
            })
        }

        const reqOptions = {
            headers: { Authorization: `Bearer ${jwt}`, },
        };
        let user=await fetch("/api/v1/me/",reqOptions);
        if (!user.ok){
            await this.setState({
                authSetted:true,
                auth:false
            })
        
        }
        else{
            await this.setState({
                authSetted:true,
                auth:true
            })
        }
    }

    async handleNameChange(event) {
        console.log(event.target.value)
        await this.setState({
            name: event.target.value
        });
    }
    async handleAuthorChange(event) {
        console.log(event.target.value)
        await this.setState({
            author: event.target.value
        });
    }
    async handlePriceChange(event) {
        console.log(event.target.value)
        await this.setState({
            price: event.target.value
        });
    }
    handlePhotoChange(e) {
        console.log("changed")
        if (document.getElementById("inputGroupFile02").value!=''){
        let text=document.getElementById("inputGroupFile02").value.replace('C:\\fakepath\\', " ");
        document.getElementById("need").innerText=text
        }else{
            document.getElementById("need").innerText="Choose file*"
        }
    } 
    async handleSubmit(e) {
        e.preventDefault();
        const jwt = localStorage.getItem('jwt');
        if (!jwt) {
            await this.setState({
                authSetted:true,
                auth:false
            })
        }
        const reqOptions = {
            headers: { Authorization: `Bearer ${jwt}`, },
        };
        const formData = new FormData(e.target);
        try {
            const response = await fetch('/api/v1/photos/', {
                method: 'POST',
                body: formData,
                headers: { Authorization: `Bearer ${jwt}`, }
            });
            if(response.ok){
                await this.setState({
                    authSetted:true,
                    auth:true
                })
                this.props.history.push("/photos")
            }
            const result = await response.json();
            console.log(':', JSON.stringify(result));
            if (result.error && result.error!=undefined){
                if(result.status=401)
                    await this.setState({
                        authSetted:true,
                        auth:false
                    })
                if (result.error == "there is already photo with such name")
                    await this.setState({
                        serverError: result.error
                    })
                else {
                    console.log(result.error)
                    await this.setState({
                        serverError: "bad input"
                    })
                }
            }
        } catch (error) {
            console.error(':', error);
        }
    }

    render() {
        if(!this.state.authSetted)
            return(<div></div>)
        if(!this.state.auth)
            return <NoAuth />
        let errorText
        if(this.state.serverError)
            errorText=<div class="errorBox">{this.state.serverError}</div>
        return (
            <div class="newPhotoStyle">
                <br />
                <br /><br />
                <p class="font-weight-bolder">Create new photo</p>
                {errorText}
                
                <form id="form" onSubmit={this.handleSubmit} enctype="multipart/form-data">
                    <div class="form-group">
                        <div class="input-group mb-3">
                            <div class="input-group-prepend">
                                <span class="input-group-text" id="inputGroup-sizing-default">name*</span>
                            </div>
                            <input type="text" name="name" placeholder="name" onChange={this.handleNameChange} required class="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default" />
                        </div>
                    </div>
                        <div class="input-group mb-3">
                            <div class="input-group-prepend">
                                <span class="input-group-text" id="inputGroup-sizing-default">author</span>
                            </div>
                            <input type="text" name="author" placeholder="author" onChange={this.handleAuthorChange} class="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default" />
                        </div>
                        <div class="input-group mb-3">
                            <div class="input-group-prepend">
                                <span class="input-group-text" id="inputGroup-sizing-default">price</span>
                            </div>
                            <input type="number" id="price" name="price" placeholder="price" onLoadedData={this.handlePriceChange} class="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default" />
                        </div>
                        <div class="input-group mb-3">
                            <div class="custom-file">
                                <input type="file" required class="custom-file-input butt" onChange={this.handlePhotoChange} name="url" accept=".jpg,.jpeg,.png" id="inputGroupFile02" />
                                <label id="need" class="custom-file-label butt"  for="inputGroupFile02">Choose file*</label>
                            </div>
                        </div>
                        <div class="form-group">
                            <div class="input-group mb-3">
                                <div class="input-group-prepend">
                                    <span class="input-group-text" id="inputGroup-sizing-default">comment</span>
                                </div>
                                <textarea type="text" name="description" placeholder="/description" class="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default"></textarea>
                            </div>
                        </div>
                        <button class="btn btn btn-primary btn-block" type="submit">post</button>
            </form>
                    </div>


                    );
                  }
              }
            
  export default NewPhoto;