
import React, { useState, useEffect } from 'react';
import './albums.css'
import {
    Link
} from "react-router-dom";
import NoAuth from '../errorPages/noAuth';
class Albums extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            page: 1,
            tag: '',
            pageNum: 1,
            nextPage: false,
            prevPage: true,
            listAlbums: [],
            season:"any",
            auth:false,
            authSetted:false
        };
        this.proceedState();
        this.radiochange=this.radiochange.bind(this);
        this.handleTagChange = this.handleTagChange.bind(this);
        this.handleNext = this.handleNext.bind(this);
        this.handlePrev = this.handlePrev.bind(this);
    }

    async radiochange(e){
        console.log(e.target.value)
        await this.setState({
            page:1,
            season:e.target.value
        })
        await this.proceedState()
    }

    async proceedState() {
        console.log("ok boomer")
        const jwt = localStorage.getItem('jwt');
        if (!jwt) {
            this.setState({
                authSetted:true,
                auth:false
            })
        }
        let text=""
        if(this.state.season!="any")
            text="&season="+this.state.season
        const reqOptions = {
            headers: { Authorization: `Bearer ${jwt}`, },
        };
        Promise.all([
            fetch("/api/v1/albums?searching=" + this.state.tag + "&page=" + this.state.page+text, reqOptions).then(x => x.json()),
            fetch("/api/v1/me",reqOptions)
        ])
            .then(async ([itemsData,me]) => {
                if (me.ok)
                this.setState({authSetted:true,auth:true})
                else    
                this.setState({authSetted:true,auth:false})
                console.log("num ofpages")
                console.log(itemsData.pageNumber)
                await this.setState({ pageNum: itemsData.pageNumber })
                if (parseInt(this.state.page) > 1)
                    await this.setState({ prevPage: true });
                else
                    await this.setState({ prevPage: false });
                if (this.state.page < this.state.pageNum)
                    await this.setState({ nextPage: true });
                else
                    await this.setState({ nextPage: false });
                console.log(itemsData.displayData)
                if (itemsData.displayData != undefined)
                    this.setState({ listAlbums: itemsData.displayData })
                else
                    this.setState({ listAlbums: [], pageNum: 1 })

                console.log(this.state.listAlbums)
            })
    }
    async handleTagChange(event) {
        console.log("event")
        await this.setState({
            tag: event.target.value,
            page: 1
        })
        console.log(this.state.tag)
        await this.proceedState();
    }
    async handleNext(event) {
        console.log("event")
        let page = this.state.page + 1
        await this.setState({
            page: page
        })
        await this.proceedState();
    }
    async handlePrev(event) {
        console.log("event")
        let page = this.state.page - 1
        await this.setState({
            page: page
        })
        await this.proceedState();
    }
    render(

    ) {
        if(!this.state.authSetted){
            return (<div></div>)
        }
        if(!this.state.auth){
            return (<NoAuth/>)
        }
        let button1;

            while (this.state.listAlbums.length < 3) {
                let albums = this.state.listAlbums
                albums.push(null)
            }
        if (this.state.listAlbums.length != 0) {
            if (this.state.prevPage)
                button1 = <button onClick={this.handlePrev} id="prevButton" class="btn btn-outline-dark" >prev</button>
            else
                button1 = <button onclick="prev()" id="prevButton" class="btn btn-outline-dark" disabled>prev</button>
        }
        let button2;
        if (this.state.listAlbums.length != 0) {
            if (this.state.nextPage)
                button2 = <button onClick={this.handleNext} id="prevButton" class="btn btn-outline-dark" >next</button>
            else
                button2 = <button id="prevButton" class="btn btn-outline-dark" disabled>next</button>
        }
        return (
            <div>
                <br />
                <div class="form-inline mt-2 mt-md-0 marginlelt">
                    <label for="searh" id="heading" >current album list:
    </label>
                    <input onChange={this.handleTagChange} class="form-control mr-sm-2 marginleltmore" id="search" type="text" placeholder="Search" name="searching" />
                </div>
                <br />
                <br />
                <div class="album py-4 bg-light">
                    <div class="container">
                        <div class="row" id="parentElement">

                            {this.state.listAlbums.map(album => {

                                if (album != null) {
                                    console.log("album")
                                    console.log(album)
                                    return (

                                        <div class="col-md-3" style={{ display: "inline-block" }}>
                                            <div class="card mb-3 shadow-sm">
                                                <svg class="bd-placeholder-img card-img-top" width="100%" height="100%" xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="xMidYMid slice" focusable="false" role="img" aria-label="Placeholder: Thumbnail">
                                                    <image href={album.previewUrl} alt={album.name} width="100%" height="100%" x="0" y="0" />
                                                </svg>
                                                <div class="card-body">
                                                    <div class="d-flex justify-content-between align-items-center">
                                                        <div class="btn-group">
                                                            <Link to={'/albums/' + album._id} class="btn btn-sm btn-outline-secondary">View</Link>
                                                        </div>
                                                        <small class="text-muted">{album.name}</small>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>

                                    )
                                } else
                                    return (<div class="chetv"></div>)
                            })}
                             <div class="col-md-3 rightFloat">
                                 <div class="marginR">
                                season:
                                <fieldset class="form-group" onChange={this.radiochange}>
    <div class="row">
      <div class="col-sm-10">
        <div class="form-check" >
          <input class="form-check-input" type="radio" name="gridRadios" id="gridRadios1" value="any" />
          <label class="form-check-label" for="gridRadios1">
            any
          </label>
        </div>
        <div class="form-check">
          <input class="form-check-input" type="radio" name="gridRadios" id="gridRadios2" value="winter" />
          <label class="form-check-label" for="gridRadios2">
            winter
          </label>
        </div>
        <div class="form-check">
          <input class="form-check-input" type="radio" name="gridRadios" id="gridRadios2" value="spring" />
          <label class="form-check-label" for="gridRadios2">
            spring
          </label>
        </div>
        <div class="form-check">
          <input class="form-check-input" type="radio" name="gridRadios" id="gridRadios2" value="summer" />
          <label class="form-check-label" for="gridRadios2">
            summer
          </label>
        </div>
        <div class="form-check">
          <input class="form-check-input" type="radio" name="gridRadios" id="gridRadios2" value="fall" />
          <label class="form-check-label" for="gridRadios2">
            fall
          </label>
        </div>
      </div>
    </div>
  </fieldset>
           
                            </div></div>
                            
                            </div></div></div>
                            <p />
                            <br />
                            <div style={{ "text-align": "center" }}>
                                {button1}
                                <span id="page">{this.state.page}</span>/<span id="pagenum">{this.state.pageNum}</span>
                                {button2}
                            </div>
                        </div>
                        );
                      }
                  }
  export default Albums;