
import React from 'react';
import './photos.css'
import NoAuthErr from '../errorPages/noAuth';
import NotFound from '../errorPages/notFound';
class Photo extends React.Component {

    constructor(props) {
        super(props);
        console.log(props.match.params.id);
        this.getPhoto(props.match.params.id)
        this.state = {
            photo: {},
            user: {},
            error: false,
            can: false,
            serverError:false,
            auth:true,
            authSetted:false,
            errorStatus:200
        };
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handlePriceInput=this.handlePriceInput.bind(this)
        this.handleNameInput=this.handleNameInput.bind(this)
        this.handlePhotoChange = this.handlePhotoChange.bind(this);
        this.handleAuthorInput=this.handleAuthorInput.bind(this)
    }
    async handlePriceInput(e){
        let photo=this.state.photo
        photo.price=e.target.value
        await this.setState({ photo:photo  })
    }
    async handleSubmit(e){
        e.preventDefault(); 
        const formData = new FormData(e.target)
        const jwt = localStorage.getItem('jwt');
        if (!jwt) {
            alert("JWT is missing. Log in first");
            return;
        }
        try {
            const response = await fetch('/api/v1/photos/'+this.props.match.params.id, {
              method: 'PUT',
              body: formData,
              headers: { Authorization: `Bearer ${jwt}`, },
            });
            const result = await response.json();
            console.log('response:', JSON.stringify(result));
            if (result.error)
                if(result.error=="there is already photo with such name")
                    this.setState({serverError:result.error})
                else {
                    this.setState({serverError:"bad input"})
                }
            else(this.props.history.push("/photos/"+this.props.match.params.id))
          } catch (error) {

          }
    }
    async handleNameInput(e){
        let photo=this.state.photo
        photo.name=e.target.value
        await this.setState({ photo:photo  })
    }
    async handleAuthorInput(e){
        let photo=this.state.photo
        photo.author=e.target.value
        await this.setState({ photo:photo  })
    }
    handlePhotoChange(e) {
        console.log("changed")
        if (document.getElementById("inputGroupFile02").value!=''){
        let text=document.getElementById("inputGroupFile02").value.replace('C:\\fakepath\\', " ");
        document.getElementById("need").innerText=text

        }else{
            document.getElementById("need").innerText="Choose file"
        }
    } 
    async getPhoto(id) {
        const jwt = localStorage.getItem('jwt');
        if (!jwt) {
            alert("JWT is missing. Log in first");
            await this.setState({authSetted:true,auth:false})
            return;
        }
        const reqOptions = {
            headers: { Authorization: `Bearer ${jwt}`, },
        };

        let user = await fetch("/api/v1/me/", reqOptions);
        if (user.ok) {
            await this.setState({authSetted:true,auth:true})
            user = await user.json();
            console.log("user")
            console.log(user)
            await this.setState({ user: user })
        } else {
            await this.setState({authSetted:true,auth:false})
            await this.setState({ error: "not authorized",errorStatus:401 })
            return
        }
        let response = await fetch("/api/v1/photos/" + id, reqOptions);
        if (response.ok) {
            response = await response.json();
            console.log(response)
            await this.setState({ photo: response })
        } else {
            
            console.log("hmm")
            await this.setState({ error:"no such photo",errorStatus:404 })
            return;
        }

        let condition = false
        if (this.state.photo)
            if (this.state.photo.owner)
                condition = (this.state.photo.owner.toString() == this.state.user.id.toString()) 
        if(this.state.user.role)
            condition=true
        await this.setState({
            can: condition
        })
    }

    // async handleDelete(event) {
    //     console.log("event")

    // }

render()
{
    if(!this.state.authSetted)
            return(<div></div>)
    let error
    if(this.state.errorStatus==401)
            return(<NoAuthErr/>)
    if(this.state.errorStatus==404)
            return(<NotFound/>)
    if (!this.state.can)
        error = "you don't have rights to edit this photo"
    if (this.state.error)
        error = this.state.error
    
    let text
    if (error)
        text = <div class="errorBox">{error}</div>
    else {
        text = <div class="photoContainer">
            <div class="new"><img src={this.state.photo.url} class="img-thumbnail photo1"/></div>
                <div class="photoInfo">
                    {this.state.serverError}
                    <form onSubmit={this.handleSubmit} enctype="multipart/form-data">
                        <div class="input-group mb-3">
                            <div class="input-group-prepend">
                                <span class="input-group-text" id="inputGroup-sizing-default">name</span>
                            </div>
                            <input type="text" onChange={this.handleNameInput} value={this.state.photo.name} name="name" placeholder="name" required class="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default"/>
                        </div>
                            <div class="input-group mb-3">
                                <div class="input-group-prepend">
                                    <span class="input-group-text" id="inputGroup-sizing-default">author</span>
                                </div>
                                <input type="text" onChange={this.handleAuthorInput} value={this.state.photo.author} name="author" placeholder="author" required class="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default"/>
             </div>
                                <div class="input-group mb-3">
                                    <div class="input-group-prepend">
                                        <span class="input-group-text" id="inputGroup-sizing-default">price</span>
                                    </div>
                                    <input type="number" id="price" onChange={this.handlePriceInput} name="price" value ={this.state.photo.price} placeholder="price" required class="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default"/>
             </div>
                                    <div class="input-group mb-3">
                                        <div class="custom-file">
                                            <input type="file" class="custom-file-input" name="url" onChange={this.handlePhotoChange} accept=".jpg,.jpeg,.png" id="inputGroupFile02" />
                                            <label class="custom-file-label butt" id="need" for="inputGroupFile02">Choose file</label>
                                        </div>
                                    </div>
                                    <button class="btn btn btn-primary btn-block" type="submit">save changes</button>
             </form>
                            </div>
                        </div>
                        
                        }
                        return (
                            <div>
                            {text}
                            </div>
                        );
                      }}
                  
  export default Photo;