import React,{useState,useEffect} from 'react';
import Success from '../errorPages/success'
import NoAuth from '../errorPages/noAuth'
export default function Telega(props) {
    const jwt = localStorage.getItem('jwt');
    const [auth, setAuth] = useState(false);
    useEffect(() => {
        getUser()
    }, [])
    async function getUser(){
        const jwt = localStorage.getItem('jwt');
     if (!jwt) {
            return;
        }
        const reqOptions = {
            method:"POST",
            headers: { Authorization: `Bearer ${jwt}`, },
        };
        let params = (new URL(document.location)).searchParams;
        // console.log("id")
        // console.log(params.get("id"))
        let response = await fetch ("/api/v1/telegram?chat_id="+params.get("id"),reqOptions)
        if(response.ok)
            setAuth(true)

    }
    if(auth)
    return (
        <Success/>
    )
    else
    return(
        <NoAuth/>
    )
}
