
import React  from 'react';
import   './users.css'
import NoAuthErr from '../errorPages/noAuth';
import NotFound from '../errorPages/notFound';
import {
    Link
  } from "react-router-dom";
class User extends React.Component {

    constructor(props) {
        super(props);
        console.log(props.match.params.id);
        this.getUser(props.match.params.id)
        this.state = {
            previewUser:{},
            user:{},
            editButton:false,
            deleteButton:false,
            errorStatus:200,
            auth:false,
            authSetted:false
        };
        this.handleDelete = this.handleDelete.bind(this);
        this.handleChangeRights = this.handleChangeRights.bind(this);
      }
            
      async getUser(id){
        const jwt = localStorage.getItem('jwt');
        if (!jwt) {
            await this.setState({authSetted:true})
            return
        }
        const reqOptions = {
            headers: { Authorization: `Bearer ${jwt}`, },
        };
        
        let user=await fetch("/api/v1/me/",reqOptions);
        if (user.ok){
            user=await user.json();
            await this.setState({authSetted:true,auth:true})
            console.log("user")
            console.log(user)
            await this.setState({user:user})
        }else{
            await this.setState({authSetted:true,auth:false})
            return
        }
        let response= await fetch("/api/v1/users/"+id,reqOptions);
        console.log("response:")
        console.log(response)
        if (response.ok){
            response=await response.json();
            console.log(response)
            if(!response.avaUrl)
                response.avaUrl="http://res.cloudinary.com/dksfym6ok/image/upload/v1575476141/asd.png"
            await this.setState({previewUser:response})
        }else{
            await this.setState({serverError:404})
        }
        
        let condition=false
        if(this.state.previewUser)
            if(this.state.previewUser.owner)
                condition=(this.state.previewUser.owner.toString()==this.state.user.id.toString() )||this.state.user.role
        await this.setState({
            editButton:condition,
            deleteButton:condition
        })
      }
      async handleDelete(event){
        console.log("changing")

    
    }

      async handleChangeRights(event){
        console.log("changing")
        let newuser=this.state.previewUser;
        newuser.role=!this.state.previewUser.role
        await this.setState({previewUser:newuser})
        const jwt = localStorage.getItem('jwt');
        const reqOptions = {
            headers: { Authorization: `Bearer ${jwt}`, },
            method: 'PUT'
        };
        
        let user=await fetch("/api/v1/users/"+this.props.match.params.id+"?role="+this.state.previewUser.role,reqOptions);
        if (user.ok){
            user=await user.json();
            console.log("user")
            console.log(user)
        }
    
    }
    render() 
      {
        if(!this.state.authSetted)
        return(<div></div>)
        if(!this.state.auth)
            return(<NoAuthErr/>)
        if(this.state.serverError==404)
            return(<NotFound/>)
        let Mybuttons;
        // console.log(this.state.user)
        // console.log(this.state.previewUser)
        if(this.state.user.id==this.state.previewUser.id ){
            Mybuttons=<div><Link to="/myphotos"><input class="btn btn-secondary" type="submit" value="my photos"/></Link>    
            <br/>
            <br/></div> 
        
        }
        let editb
        if(this.state.user.id==this.state.previewUser.id || this.state.user.role ){
            editb=<div><Link to={'/editusers/'+this.state.previewUser.id}> <input class="btn btn-secondary" type="submit" value="edit"/></Link>
                            <br/>
                            <br/></div>
        }
        let rigthsB;
        if(this.state.user.role){
            if(!this.state.previewUser.role){
                rigthsB=<input class="btn btn-secondary" onClick={this.handleChangeRights} type="submit" value="switch rigths to superuser"></input>
            }else{
                rigthsB=<input class="btn btn-secondary" onClick={this.handleChangeRights}  type="submit" value="switch rigths to ordinary user"></input>
            }
        }
        
        return (
            <div class="container magrinTop30" >
                <div class="marginLeft20">
                <div class="row">
                    <div class="col-sm-4">

                    <div class="img"><img src={this.state.previewUser.avaUrl} class="img-thumbnail userImage" /></div>
                    </div>
                    <div class="col-sm-4" class="padiingLeft">
                    <h3>About User</h3>
                    Login: {this.state.previewUser.login}
                    <p>Fullname: {this.state.previewUser.fullname}</p>
                    {Mybuttons}
                    {editb}
                    {rigthsB}
                    </div>
                </div>
                </div>
                </div>
    


        );
      }}
  
  export default User;